body {
  background-color: #000;
}
.container {
  height: 100vh;
  width: 100%;
  background-image: url("../../public/bg.png");
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #aaa;
}

.nav {
  height: 50px;
  width: 90%;
  max-width: 1100px;
  margin: 0.5rem auto;
  border-radius: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #333;
  box-shadow: 0px 3px 20px rgba(255,255,255,0.1);
  z-index: 6;
}

.nav ul {
  display: flex;
  justify-content: flex-end;
}

.nav li {
  list-style: none;
  margin: 0.3rem;
  cursor: pointer;
}

.nav li:hover {
  color: dodgerblue;
}

.controls {
  align-self: start;
}

.main {
  border: 1px solid red;
  height: 100vh;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  overflow: scroll;
  max-width: 1100px;
  /* background-image: linear-gradient(30deg, #211010, #5e2222); */
  /* background-color: #333; */
  scroll-snap-type: y mandatory;
}

.card {
  margin: 0.5rem;
  min-height: 50%;
  height: min;
  width: 300px;
  background-image: linear-gradient(30deg, #211010, #5e2222);
  box-shadow: 0px 3px 10px #fff1;
  border: 1px solid #666;
  border-radius: 0.5rem;
  overflow: hidden;
  cursor: pointer;
  scroll-snap-align: start;
}

.image {
  height: 200px;
  width: 100%;
  background-image: url('../../public/web.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  filter: grayscale(100%);
}

.footer {
  font-size: 0.7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
  margin: 0.5rem auto 0.1rem auto;
  padding: 0.3rem;
  background-image: linear-gradient(30deg, #211010, #5e2222);
  border-radius: 0.3rem;
}

